/**
 * ----------------------------------------
 * Animation: fade-in-top
 * ----------------------------------------
 */

.ani--fade-in__top {
   animation-name: fade-in-top;
}

@keyframes fade-in-top {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
